/* eslint-disable */
export default {
  install (Vue) {
    const noSleep = document.createElement('video');
    noSleep.setAttribute('id', 'no-sleep');
    noSleep.setAttribute(
      'style',
      'position: absolute; top: -10px; left: -10px;'
    );
    noSleep.setAttribute('loop', '');
    noSleep.setAttribute('playsinline', '');
    noSleep.setAttribute('height', '10');
    noSleep.setAttribute('width', '10');
    document.body.appendChild(noSleep);

    // Create the source element (child of video el) with the source of the mp4
    const noSleepSource = document.createElement('source');
    noSleepSource.setAttribute('type', 'video/mp4');
    noSleepSource.setAttribute(
      'src',
      'data:video/mp4;base64,'
    );
    document.getElementById('no-sleep').appendChild(noSleepSource);

    // Register the noSleep method in all components
    Vue.mixin({
      methods: {
        noSleep() {
          return {
            on() {
              noSleep.play();
            },
            off() {
              noSleep.pause();
            }
          };
        }
      }
    });
  }
};